import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Select } from 'antd';
import {
  addSubjectGroup,
  getSubjectGroupById,
  editSubjectGroup,
  getFaculty,
} from 'services/Subject';

const { Option } = Select;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    subjectName: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormSubject extends Component {
  state = {
    teacherId: false,
    initialValue: {},
  };

  async componentWillMount() {
    const dataFaculty = await getFaculty();

    this.setState({
      listDataFaculty: dataFaculty.data.items,
    });
  }

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  getTeacherById = async code => {
    const teacherMap = await getSubjectGroupById(code);
    const initialValue = handleInitialValue(teacherMap.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    const { teacherId } = this.state;
    this.props.form.validateFields(async (err, values) => {
      if (teacherId) {
        await editSubjectGroup(values);
        this.props.history.goBack();
      } else {
        await addSubjectGroup(values);
        this.props.history.goBack();
      }
    });
  };

  render() {
    const { initialValue, listDataFaculty } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    return (
      <Card title={teacherId ? 'Cập nhật danh mục bộ môn' : 'Thêm danh mục bộ môn'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Mã bộ môn" {...formItemLayout}>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên bộ môn" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khoa" hasFeedback>
              {getFieldDecorator('facultyId', {
                initialValue: initialValue.facultyId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  {listDataFaculty &&
                    listDataFaculty.map(item => (
                      <Option key={item.id} value={item.id}>
                        {item.label}
                      </Option>
                    ))}
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#/subject/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSubject {...props} />
  </Page>
));



