import React, { useState } from 'react';
import { Button } from 'antd';
import './style.scss';

export default ({ className = '', title = 'Lưu dữ liệu', ...otherProps }) => {
  const [loading, setloading] = useState(false);
  const onClick = async () => {
    if (!otherProps.onClick) {
      return;
    }
    setloading(true);
    await otherProps.onClick();
    setloading(false);
  };

  return (
    <Button
      disabled={loading}
      loading={loading}
      {...otherProps}
      onClick={onClick}
      className={`btnAction btnSave ${className}`}
      icon="save"
    >
      {title}
    </Button>
  );
};



